                        </p>
                        <ul class="cloudcompute-list">
                            <li>
                              <router-link :to="{name: 'PhysicaMachineEnv'}"></router-link>
                            </li>
                            <span class="line">|</span>
                            <li>
                              <router-link :to="{name: 'PhysicaMachineEnv'}"></router-link>
                            </li>
                            <span class="line">|</span>
                            <li>
                              <router-link :to="{name: 'PhysicaMachineEnv'}"></router-link>
                            </li>
                        </ul>
                    </div>
