                            <p class="row2-part-title">{{item.title}}</p>
                            <p class="row2-part-info">{{item.info}}</p>
                            <div class="row2-part-btn">
                                
                            </div>
                        </div>                      
                    </div>
               </div>
            </div>
          </div>
          <!--  -->
