<script>


export default {
  name: 'app',
  data() {
    return {
       banners:['banner3','banner2','banner1'],
       applicationManagementInfos:[
          {'iconName':'fa-cloud','title':'','info':''},
          {'iconName':'fa-eercast','title':'','info':'SAP HANASAP'},
          {'iconName':'fa-bandcamp','title':'','info':''},
          {'iconName':'fa-podcast','title':'','info':'SAP HANASAP'}
        ],
        solutionInfos:[
          {'title':'',
           'detail':'',
           'infos':[
              {'title':'','detail':'','className':'imgShow1'},
              {'title':'','detail':'','className':'imgShow2'}
          ]},
          {'title':'BOSS',
           'detail':'',
           'infos':[
              {'title':'','detail':'','className':'imgShow3'},
              {'title':'','detail':'','className':'imgShow4'}
          ]

          }
        ],
        mirrors:[
          {'title':'','info':'IaaSctyun','position':'positionImg1'},
          {'title':'','info':'','position':'positionImg2'},
          {'title':'','info':'SDNVPC100%','position':'positionImg3'}
        ],
        serviceInfos:[
          {'tabTitle':'','icon':'serviceIcon1','detail':'4.0O2O','submenus':[
              {'title':'','detail':'','img':'serviceImg1'},
              {'title':'','detail':'','img':'serviceImg6'},
              {'title':'','detail':'','img':'serviceImg3'},
              {'title':'','detail':'','img':'serviceImg8'}             
            ]},
          {'tabTitle':'','icon':'serviceIcon2','detail':'','submenus':[
              {'title':'','detail':'','img':'serviceImg5'},
              {'title':'','detail':'','img':'serviceImg2'},
              {'title':'','detail':'','img':'serviceImg7'},
              {'title':'NOSQL','detail':'','img':'serviceImg4'}
            ]},
          {'tabTitle':'','icon':'serviceIcon3','detail':'','submenus':[
              {'title':'','detail':'','img':'serviceImg1'},
              {'title':'','detail':'','img':'serviceImg5'},
              {'title':'','detail':'','img':'serviceImg3'}
            ]}
        ],
        apiArr:[
          {'title':'API','img':'apiImg1','detail':''},
          {'title':'API','img':'apiImg2','detail':''},
          {'title':'API','img':'apiImg3','detail':''}
        ],
        isActiveKey:0,
        timer:null
    };
  },
  methods: {
    clearTimer(){
      clearInterval(this.timer)
    },
    startTimer(){
      this.timer=setInterval(()=>{
        if(this.isActiveKey == (this.serviceInfos.length-1)){
          this.isActiveKey =0
        }else{
          this.isActiveKey++
        }      
      },2000)
    }
  },
  created(){
    this.startTimer()
  },
  mounted(){
    
  }
}
</script>
<style scoped>
      /*  */
      .banner{width: 100%;height:100%;background-repeat: no-repeat;background-position: center center;background-size:60% 100%}
      .banner1{background-image:url('../assets/home/banner-1.png');background-color: #2ba5d4 }
      .banner2{background-image:url('../assets/home/bannner2.png');background-color: #2ca3d4}
      .banner3{background-image:url('../assets/home/banner-3.png');background-color: #1cb9d5 }
